	module.exports = {"data":{"markdownRemark":{"html":"<p>If you’ve never heard of it, Blender is an amazing, free, open source, full featured 3d software toolset. I’ve used it for 3d animation, 3d printing, and design.</p>\n<p>You might use it to, say… painstakingly build a 3d model of yourself with a functioning skeleton and then animate it using motion capture data from Carnegie Melon’s MOCAP library.</p>\n<img src=\"https://media.giphy.com/media/l4EpgJvud8PWq1eYE/giphy.gif\" width=\"100%\"/>\n<p>Or maybe you have more pressing things to do with your time.</p>\n<p>One of the interesting features of Blender is its Python interface, where you can script things like task animation, tool sets, and game logic. One of the areas of Blender I am keen to explore is its game engine. It’s actually one of the reasons I became interested in coding in the first place.</p>\n<p>This will be a brief tutorial on setting up Blender for scripting, while future posts will dive more in depth into programming the game engine.</p>\n<p>So the first thing you want to do is download the latest stable release of Blender from <a href=\"https://www.blender.org/\">https://www.blender.org/</a>. At the time of writing this, I’m using version 2.79.</p>\n<p>Once you’ve downloaded and copied to your applications folder, right click on the Blender program, select Show Package Contents / Contents / MacOS and then right click on the blender exec file and make an alias. You can put this alias in an accessible location. Double click on the executable to open Blender through the console.\n</br>\n</br>\n<a href=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515218730/4_xt4zwm.jpg\">\n<img src=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515218730/4_xt4zwm.jpg\"\n     alt=\"blender exec\"\n     style=\"width: 100%\" />\n</a>\n</br>\n</br>\nOnce in Blender, you can change the layout to the scripting default. This makes available a text editor and python console as well as the usual 3d view.\n</br>\n</br>\n<a href=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515218730/2_tya4ul.jpg\">\n<img src=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515218730/2_tya4ul.jpg\"\n     alt=\"blender exec\"\n     style=\"width: 100%\" />\n</a>\n</br>\n</br>\nIf you hover over the console and press ctrl + up-arrow, you can maximize the partition. This is a fully functioning Python console. Observe while I print yet another “Hello World” to yet another console!!\n</br>\n</br>\n<a href=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515219398/5_sg4hcw.png\">\n<img src=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515219398/5_sg4hcw.png\"\n     alt=\"blender exec\"\n     style=\"width: 100%\" />\n</a>\n</br>\n</br>\nIn the text editor, hit the <code>+New</code> button to create a new text block. Type the following to implement your first Blender related script:</p>\n<pre><code>import bpy\n#makes available everything to do with blender\nbpy.ops.object.duplicate()\n</code></pre>\n<p>Click run script and look in the 3d viewport. Right click on the cube and press <code>g</code> to grab. You duplicated the model!\n</br>\n</br>\n<a href=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515219825/6_a8n44e.png\">\n<img src=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515219825/6_a8n44e.png\"\n     alt=\"blender exec\"\n     style=\"width: 100%\" />\n</a>\n</br>\n</br>\nYou’ll notice the bar along the top of the window above the text editor and 3d viewport contains the log. This you can also maximize with <code>ctrl + up-arrow</code>:\n</br>\n</br>\n<a href=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515219923/7_icygdf.jpg\">\n<img src=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515219923/7_icygdf.jpg\"\n     alt=\"blender exec\"\n     style=\"width: 100%\" />\n</a>\n</br>\n</br>\n(The red line was from a previous error when I misspelled the word “duplicate” :/)</p>\n<p><strong>Back to the console!</strong></p>\n<p>The <code>import bpy</code> command gives you access to everything Blender related in Python.</p>\n<p>If you type in <code>bpy.</code> and hit <code>command + space-bar</code> you will see the list of commands for <code>bpy</code>.\n</br>\n</br>\n<a href=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515220288/8_wvbug5.png\">\n<img src=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515220288/8_wvbug5.png\"\n     alt=\"blender exec\"\n     style=\"width: 100%\" />\n</a>\n</br>\n</br>\nTo return a list of all the models in your scene:</p>\n<pre><code>models = bpy.data.objects\n#assigns a variable models to your list of blender objects\nfor model in models:\n    print(model)\n#returns all elements of your models list\n&#x3C;bpy_struct, Object(\"Camera\")>\n&#x3C;bpy_struct, Object(\"Cube\")>\n&#x3C;bpy_struct, Object(\"Cube.001\")>\n&#x3C;bpy_struct, Object(\"Lamp\")>\n</code></pre>\n<p>Here you can see we have a camera object, two cubes, and a lamp.</p>\n<p>Let’s manipulate the location of our cube object. If I type:</p>\n<pre><code>bpy.data.objects['Cube'].location = (1,2,3)\n</code></pre>\n<p>Our cube moves to the x, y, and z coordinates: 1, 2, 3.</p>\n<p>If you type <code>bpy.context.</code> and then hold <code>control + space bar</code> you will see the all the available bpy context modules:\n</br>\n</br>\n<a href=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515220441/9_ewjrbm.png\">\n<img src=\"http://res.cloudinary.com/dwnehv6tb/image/upload/v1515220441/9_ewjrbm.png\"\n     alt=\"blender exec\"\n     style=\"width: 100%\" />\n</a>\n</br>\n</br>\nThe context modules relate to what the user has access to at that exact moment. i.e. what windows are open and what objects are in the 3d viewport.</p>\n<p>If you type <code>bpy.context.active_object</code> the console will return to you the object you have selected in the 3d viewport. The context modules will be super important moving forward with scripting in Blender.</p>\n<p>In case you missed my dancing:</p>\n<img src=\"https://media.giphy.com/media/l4EpgJvud8PWq1eYE/giphy.gif\" width=\"100%\"/>","frontmatter":{"date":"October 10, 2017","path":"/python-scripting-in-blender","title":"Python Scripting in Blender"}}},"pathContext":{}}

